import React, { useState, useEffect, useMemo } from "react";
import "./feed.css";
import "mapbox-gl/dist/mapbox-gl.css";
import Map, {
  Marker,
  Popup,
  NavigationControl,
  FullscreenControl,
  GeolocateControl,
} from "react-map-gl";
import axios from "axios";
import { useNavigate } from "react-router-dom";

import Pin from "./pin";

export default function Feed() {
  const token =
    "pk.eyJ1IjoiYm9yYXVsdWthbiIsImEiOiJjbGNneHp3ejMwdmpsM29xaGJlbzRxMndhIn0.dtiTt6bdbF3y8hOFT6mgTg";

  const [data, setData] = useState(null);
  const [users, setUsers] = useState(null);
  const [lng, setLng] = useState(0);
  const [lat, setLat] = useState(0);

  useEffect(() => {
    navigator.geolocation.getCurrentPosition(function (position) {
      console.log("Latitude is :", position.coords.latitude);
      setLat(position.coords.latitude);
      console.log("Longitude is :", position.coords.longitude);
      setLng(position.coords.longitude);
    });
    async function getData() {
      try {
        const res = await axios.get("http://localhost:3001/users/getUsers");
        console.log(res.data);
        setData(res.data);
      } catch (error) {
        console.error(error);
      }
    }
    getData();
  }, []);

  useEffect(() => {
    if (data) {
      setUsers(data.map((option) => option));
    }
  }, [data]);

  const [selectedUser, setSelectedUser] = useState(null);

  const pins = useMemo(
    () =>
      users?.map((user, index) => (
        <Marker
          key={user._id}
          longitude={user.locationLNG}
          latitude={user.locationLAT}
          anchor="bottom"
          onClick={(e) => {
            // If we let the click event propagates to the map, it will immediately close the popup
            // with `closeOnClick: true`
            e.originalEvent.stopPropagation();
            setSelectedUser(user);
            console.log(user._id);
          }}
        >
          <Pin />
        </Marker>
      )),
    [users]
  );

  return (
    <div className="feed">
      <div className="feedWrapper">
        <Map
          mapboxAccessToken={token}
          style={{
            width: "600px",
            height: "600px",
            borderRadius: "25px",
            border: "2px solid red",
            zoom: 1,
          }}
          initialViewState={{
            longitude: lng,
            latitude: lat,
            zoom: 1,
          }}
          mapStyle="mapbox://styles/mapbox/dark-v9"
        >
          <NavigationControl position="bottom-right" />
          <FullscreenControl />
          <GeolocateControl />
          {pins}

          {selectedUser && (
            <Popup
              anchor="top"
              longitude={selectedUser?.locationLNG}
              latitude={selectedUser?.locationLAT}
              onClose={() => setSelectedUser(null)}
            >
              <div className="PopUp">
                <p>{selectedUser?.email}</p>
                <p>{selectedUser?.username}</p>
              </div>
              {/* <img width="100%" src={selectedUser?.image} /> */}
            </Popup>
          )}
        </Map>
      </div>
    </div>
  );
}
