import axios from "axios";
import { useState, useEffect } from "react";
import "./register.css";
import { useNavigate } from "react-router-dom";

export default function Register() {
  const navigate = useNavigate();

  const [username, setUsername] = useState();
  const [email, setEmail] = useState();
  const [password, setPassword] = useState();
  const [profilePicture, setProfilePicture] = useState(null);
  const [locationLAT, setLocationLAT] = useState(0);
  const [locationLNG, setLocationLNG] = useState(0);
  /*   const passwordAgain = useState(); */

  /*   const handleClick = async (e) => {
    e.preventDefault();
    if (passwordAgain.current.value !== password.current.value) {
      passwordAgain.current.setCustomValidity("Passwords don't match!");
    } else {
      const user = {
        username: username.current.value,
        email: email.current.value,
        password: password.current.value,
      };
      try {
        await axios.post("/users/register", user);
      } catch (err) {
        console.log(err);
      }
    }
  }; */

  useEffect(() => {
    navigator.geolocation.getCurrentPosition(function (position) {
      console.log("Latitude is :", position.coords.latitude);
      setLocationLAT(position.coords.latitude);
      console.log("Longitude is :", position.coords.longitude);
      setLocationLNG(position.coords.longitude);
    });
  }, []);

  const handleSubmit = async (e) => {
    e.preventDefault();
    /*     if (passwordAgain.current.value !== password.current.value) {
      passwordAgain.current.setCustomValidity("Passwords don't match!");
    } else { */
    const user = {
      username: username,
      email: email,
      password: password,
      profilePicture: profilePicture,
      locationLNG: locationLNG,
      locationLAT: locationLAT,
    };
    try {
      await axios.post("http://localhost:3001/users/register", user);
      console.log("successfully saved " + user.username + " " + user.email);
    } catch (err) {
      console.log(err);
      console.log("catched an error");
    }
    /* } */
    navigate("/login");
  };

  return (
    <div className="login">
      <div className="loginWrapper">
        <div className="loginLeft">
          <h3 className="loginLogo">Bruh Social</h3>
          <span className="loginDesc">
            Connect with friends and the world around you on Bruh Social.
          </span>
        </div>
        <div className="loginRight">
          <form className="loginBox" onSubmit={handleSubmit}>
            <input
              placeholder="Username"
              required
              onChange={(e) => setUsername(e.target.value)}
              className="loginInput"
            />
            <input
              placeholder="Email"
              required
              onChange={(e) => setEmail(e.target.value)}
              className="loginInput"
              type="email"
            />
            <input
              placeholder="Password"
              required
              onChange={(e) => setPassword(e.target.value)}
              className="loginInput"
              type="password"
              minLength="6"
            />
            <label className="fileLabel" htmlFor="file">
              <span>
                Upload your avatar <input type="file" />
              </span>
            </label>
            {/*             <input
              placeholder="Password Again"
              required
              ref={passwordAgain}
              className="loginInput"
              type="password"
            /> */}
            <button className="loginButton" type="submit">
              Sign Up
            </button>
            <button className="loginRegisterButton">Log into Account</button>
          </form>
        </div>
      </div>
    </div>
  );
}
